@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");
* {
  margin: 0;
  border: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif

}
body{
    margin: 0;
    padding: 0;
    height: 100%;
    background-color: #ff8177;
    background-image: linear-gradient(rgba(253, 190, 135, 1) 0%, rgba(174, 217, 224, 1) 100%);
    background-size: cover; /* Set background size to cover */
}
.photo-box {
  position: relative;
  width: 350px;
  overflow: hidden;
  gap: 2rem;
  border-radius: 20px;
  margin-top: 5rem;
}
.title-main{

}
.photo {
  width: 100%;
  height: 400px;
  display: block;
  border-radius: 20px;
  object-fit: cover;
}

.text-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: 20px;
}

.photo-box:hover .text-overlay {
  opacity: 1;
  border-radius: 20px;
}

.text-overlay p {
  color: #fff;
  text-align: center;
  font-size: 18px;
  margin: 0;
}
.image-container {
  position: relative;
  border-radius: 20px;
}
.image-container span {
  position: absolute;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.7);
  width: 100%;
  height: 2rem;
  font-family: Arial, sans-serif;
  font-size: 14px;
  color: #333;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.container {
  width: 100%;
  margin-top: 3rem;

  display: flex;
  justify-content: center;
  gap: 2rem;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: row;
}
.app{
    padding: 4rem;

    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.popup-card{
    position: fixed;
    top: 50%;  /* Adjust as needed */
    left: 50%; /* Adjust as needed */
    transform: translate(-50%, -50%);
    height: 70vh;
    width: 40vw;
    background-color: rgba(120, 120, 120, 0.906);
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.176);
    border-radius: 20px;
}

.card-content p{
    color: black;
    font-size: 1.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: justify;
}
.card-content{
    width: 90%;
}